.DetailChatBackground {
    width: 100vw;
    height: 100vh;
    background-color: #ffffff;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 999;
    overflow-y: scroll;
}

.DetailChatContainer {
    border: 1px solid black;
    width: 400px;
    min-height: 100vh;
    margin-top: 100px;
    margin-bottom: 100px;
    border: 1px solid lightgray;
}

.DetailChatheader {
    height: 88px;
    align-items: center;
    display: flex;
    /* Base/White */

    background: #ffffff;
    box-shadow: 0px 4px 4px -4px rgba(51, 63, 85, 0.68);
    border-radius: 0px;
    margin-bottom: 4px;
}

.DetailChatBack {
    padding-left: 20px;
    height: 25px;
    float: left;
    background-color: transparent;
    border: none;
    cursor: pointer;
}

.DetailChatOpp {
    float: left;
    margin-left: 15px;
    margin: 2px 15px;

    font-family: Spoqa Han Sans Neo;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 28px;
    /* identical to box height, or 200% */

    letter-spacing: -0.005em;

    color: #12121d;
    flex: 1;
}

.DetailChatBtn {
    background-color: transparent;
    border: none;
    cursor: pointer;
}

.DetailChatSend {
    float: right;
    margin-right: 1%;
    color: #929da9;
}

.DetailChatMore {
    margin-right: 3%;
    color: #929da9;
}

.DetailChatBody {
    padding: 16px 10px;
    align-items: center;

    display: absolute;
    /* Base/White */
    vertical-align: middle;
    background: #ffffff;
    border-bottom: 0.54px solid #d9d9d9;
}

.DetailChatFromMe {
    font-family: Spoqa Han Sans Neo;
    font-style: normal;
    font-weight: bold;
    font-size: 13px;
    line-height: 16px;

    color: #25d482;
    float: left;
}

.DetailChatToMe {
    font-family: Spoqa Han Sans Neo;
    font-style: normal;
    font-weight: bold;
    font-size: 13px;
    line-height: 16px;

    color: #929da9;
    float: left;
}

.DetailChatTime {
    font-family: Spoqa Han Sans Neo;
    font-style: normal;
    font-weight: normal;
    font-size: 11px;
    line-height: 13px;
    text-align: right;

    color: #acb3bf;
}

.DetailChatMessage {
    font-family: Spoqa Han Sans Neo;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
    /* or 133% */
    margin-top: 15px;
    text-transform: uppercase;

    color: #636973;
}